.cards
  padding: 9.2rem 0 27.0rem
  &-item
    position: relative
    z-index: 6
    height: 100%
  &-item-front
    height: 100%

    padding: .2rem
    overflow: hidden
    border-radius: 2.6rem
    position: relative
    transform-origin: -50% 50%
    animation: flipFront2 1s
    &::before
      content: ''
      position: absolute
      top: 0
      left: 0
      width: 100%
      height: 100%
      background: linear-gradient(0deg, rgba(28,171,152,1) 0%, rgba(43,40,38,1) 100%)
      z-index: 1
      transition: .4s
    &::after
      content: ''
      position: absolute
      top: 0
      left: 0
      width: 100%
      height: 100%
      background: linear-gradient(0deg, rgba(0,164,138,1) 19%, rgba(0,93,98,1) 64%)
      z-index: 0
  &-item-front__inner
    height: 100%

    padding: 4.6rem 4.2rem
    background-color: #fff
    position: relative
    z-index: 2
    border-radius: 2.6rem
    font-size: 2.1rem
    transition: .4s
    display: flex
    flex-direction: column
  &-item__title
    font-size: 3.5rem
    font-weight: 500
    color: #060606
    margin-bottom: 1.6em
    transition: .4s
  &-item__text

  &-item__subtitle
    font-weight: 600
    color: $accent-color
    padding-top: 2.0rem
    position: relative
    margin: 2.0rem 0
    transition: .4s
    &::before
      content: ''
      position: absolute
      top: 0
      left: 0
      border-radius: .9rem
      width: 4.4rem
      height: .4rem
      background-color: $accent-color
      transition: .4s
  &-item-back
    position: absolute
    top: 0
    left: 0
    z-index: -1
    width: 100%
    height: 100%
    overflow: hidden
    border-radius: 2.6rem
    transition: .4s
    transform-origin: -50% 75%
    animation: flipBack2 1s
    img
      width: 100%
      height: 100%
      object-fit: cover
  &-item-close
    opacity: 0
    transition: .3s
    transition-delay: 1.2s

  &-item.active
    z-index: 10
    .cards-item
      &-front
        animation: flipFront 1.2s
        &::before
          opacity: 0
      &-front__inner
        background: transparent
        color: #fff
      &__subtitle
        color: #fff
        &::before
          background-color: #fff
      &__title
        color: #fff
      &-back
        animation: flipBack 1.2s
        z-index: 2
      &__link
        color: #fff
        &::before
          background-color: #fff
    .cards-item-close
      opacity: 1
      transition-delay: 1.2s
@keyframes flipFront
  0%
    transform: rotate(0)
  50%
    transform: rotate(20deg)
  100%
    transform: rotate(0)
@keyframes flipBack
  0%
    transform: rotate(0)
  50%
    transform: rotate(-20deg)
  100%
    transform: rotate(0)
@keyframes flipFront2
  0%
    transform: rotate(0)
  50%
    transform: rotate(20deg)
  100%
    transform: rotate(0)
@keyframes flipBack2
  0%
    transform: rotate(0)
  50%
    transform: rotate(-20deg)
  100%
    transform: rotate(0)
@media  ( hover: hover )

  .cards-item:hover
    z-index: 10
    .cards-item
      &-front
        animation: flipFront 1.2s

        &::before
          opacity: 0
      &-front__inner
        background: transparent
        color: #fff
      &__subtitle
        color: #fff
        &::before
          background-color: #fff
      &__title
        color: #fff
      &-back
        animation: flipBack 1.2s
        z-index: 2

@media screen and ( max-width: 992px )
  .cards
    padding: 20px 0
    &-item__title
      font-size: 20px
    &-item-front
      padding: 2px
    &-item-front__inner
      font-size: 16px
      padding: 20px 15px

    &-item-front, &-item-front__inner,  &-item-back
      border-radius: 10px
    &-item__title
      margin-bottom: 30px

    &-item__link
      margin-top: auto
      font-size: 16px
      display: inline-block

    &-item__subtitle
      padding-top: 9px
      margin: 10px 0 4px
      &::before
        width: 18px
        height: 2px
        border-radius: 2px
    &-item-close
      color: $dark-green
      border: 2px solid rgba($accent-color, 1)
      background: #AFE0D8
      font-size: 12px
      font-weight: 500
      position: absolute
      z-index: 2
      bottom: 10px
      left: 10px
      border-radius: 10px
      padding: 8px 13px
@media screen and ( max-width: 768px )
  .cards
    padding: 20px 0
    &-item__title
      font-size: 16px
    &-item-front__inner
      font-size: 12px
      padding: 20px 14px 35px
